import * as React from "react";
import Paper from "@mui/material/Paper";
import aiu from "./aiu.json";

export default function Content() {
  function aius(index) {
    const items = [];
    const keys = Object.keys(aiu[index]);
    const values = Object.values(aiu[index]);
    for (let i = 0; i < values.length; i++) {
      if (i === 0) {
        items.push(<img src={values[i]} alt="a" />);
      } else {
        items.push(
          <li>
            {keys[i]}:{values[i]}
          </li>
        );
      }
    }
    return <ul>{items}</ul>;
  }

  return (
    <Paper sx={{ maxWidth: 936, margin: "auto", overflow: "hidden" }}>
      <h1>かくとう</h1>
      <p>
        {aius(0)}
        {aius(2)}
        {aius(9)}
        {aius(10)}
      </p>
    </Paper>
  );
}
